import resultService from '../services/results';
import Menu from './Menu';
import { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Typography from '@mui/material/Typography';
import quizService from '../services/quiz';


const Results = () => {
	const [results, setResults] = useState([]);
	const [user, setUser] = useState(null);

	useEffect(() => {
		const loggedUserJSON = window.localStorage.getItem('loggedQuizUser');
		if (loggedUserJSON) {
			const user = JSON.parse(loggedUserJSON);
			setUser(user);
			quizService.setToken(user.token);
		}
		else{
			
		}
	}, []);

	useEffect(() => {
		resultService.getResults().then((response) => setResults(response));
	}, []);

	const columns = [
		{ field: 'id', headerName: 'ID', width: 90 },
		{
			field: 'user',
			headerName: 'Username',
			width: 150,
			valueGetter: (params) => {
				return params.value.username;
			},
		},
		{
			field: 'score',
			headerName: 'Score',
			width: 150,
		},
		{
			field: 'time',
			headerName: 'Time',
			width: 110,
		},
		{
			field: 'date',
			headerName: 'Date',
			width: 200,
		},
	];

	return (
		<div>
			<Menu />
			<Typography variant='h2'>Results</Typography>
			<div className='app'>
				<Box sx={{ height: 500, width: 750, backgroundColor: 'white' }}>
					<DataGrid
						rows={results}
						columns={columns}
						pageSize={5}
						rowsPerPageOptions={[5]}
						disableSelectionOnClick
						experimentalFeatures={{ newEditingApi: true }}
						sx={{
							boxShadow: 2,
							border: 3,
							borderColor: 'black',
							'& .MuiDataGrid-cell:hover': {
								color: 'purple',
							},
						}}
					/>
				</Box>
			</div>
		</div>
	);
};

export default Results;
